@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yellowtail&display=swap);
#moveUpButton{
    position:-webkit-sticky;
    position:sticky;
    top:0;
}

#moveDownButton{
    position:-webkit-sticky;
    position:sticky;
    bottom: 0;
}

html{
    background-color: rgb(255, 255, 255);
    scroll-behavior: smooth;
}

body{
    margin: 0;
    padding: 0;
}
html{
  overflow: visible;
}

.App {
  text-align: center;
  align-items: center;
  align-self: center;
}

.storage{
  overflow-y: scroll;
  -webkit-scroll-snap-type: y mandatory;
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
}


:root{
    --divWidth: 0px;
    --divHeight: 0px;
    --leftMargin: 0px;
    --topMargin: 0px;
}


.slideComponent{
    scroll-snap-align: start;
    align-self: center;
    background-color: rgba(248, 248, 248, 0.5);
    height: 0px;
    height: var(--divHeight);
    width: 0px;
    width: var(--divWidth);
    margin-left: 0px;
    margin-left: var(--leftMargin);
    margin-top: 5%;
}

.slideComponent:nth-child(1){
    margin-top : 1%;
}
.tabLink{
    text-decoration: none;
    color: black;
}

.projectTab{
    min-width: 100px;
    min-height: 50px;

    border: solid;
    border-width: 2px;
    border-radius: 25px;
    border-color: gray;

    box-shadow: 0px 0px 8px 2px gray;

    display: flex;
    flex-direction: row;
    align-items: center;
}

.projectTab:hover{
    box-shadow: 0px 0px 8px 4px rgb(250, 115, 115);
}

.imgHolder{
    width: 20%;
    height: 75%;

    margin-left: 5%;
}

img{
    border:solid;
    border-color: gray;
    border-width: 1px;
}

.tabText{
    width: 60%;
    height: 100%;
    margin-left: 5%;

    display: flex;
    flex-direction: column;
    
}

.tabTitle{
    text-align: center;
    font-weight: bold;
    font-size: large;
    font-family: 'Source Code Pro', monospace;
}
.App{
  
}

#buttonHolder{
  
}

.optionButton{
  background-color: rgb(0, 160, 252);
  color: white;
  
  font-weight: bold;
  text-align: center;
  
  border: solid;
  border-width: thin;
  border-color: black;
}

.heldButton{
  width: 50%;
  margin-top: 3px;
}

.nonContainedButton{
  width: 20%;
}

.innerHolder{
  width: 20%;
}

.controlButtonHolder{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;

  
  padding-left: 5px;

  background-color: black;
}

.controlButton{
  width: 75px;
  margin-left: 20px;
  
  border: 1px inset gray;
  
  text-align: center;
  background-color: white;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}


.controlButton:hover{
  color: aqua;
  border: 1px inset aqua;
}

.pathFinderTitle{
  background-color: black;
  color: white;

  font-family: 'MuseoModerno', cursive;
}
.dropdownmenuholder{
    position: relative;
    display: inline-block;
}

.dropDownContents{
    display: none;
    z-index: 1;

    position: absolute;
    bottom: 100%;

    background-color:'white';
}

.dropDownMenuView{
    width: 150px;

    border: solid;
    border-width: thin;

    background-color:'white';

    display: flex;
    flex-direction: row;
}

.UpTriangle{
    width: 0;
    height: 0;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-bottom: 8px solid black;

    position: absolute;
    align-self: center;
    margin-left: 85%;
}

.dropdownmenuholder:hover .dropDownContents {display: block;}
.dropItem{
    display: flex;
    justify-content: flex-start;
    align-items: center;

    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.mainContainer{
    border : solid;
    border-color: black;
    display : flex;
    flex-direction: column;
}

.titleContainer{
    border : solid;
    border-color : black;
}

.contentContainer{
    display: flex;
    flex-direction: row;
}

.appDemoContainer{
    border : solid;
    border-color: black;
}

.docContainer{
    border : solid;
    border-color: black;

    overflow-y: auto;
}

.docContainer::-webkit-scrollbar{
    width : 1%;
}

.docContainer::-webkit-scrollbar-track{
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
}

.docContainer::-webkit-scrollbar-thumb{
    -webkit-box-shadow : inset 0 0 5px rgba(0,0,0,0.75);
}
.mainBar{
    position: fixed;

    top:0;
    position:-webkit-sticky;
    position:sticky;

    background-color: rgb(0, 0, 0);
    width: 100%;
    height:40px;
    
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding-top: 5px;
}

#holdPic{
    border-radius: 50%;
    background-color: gray;
}

#holdName{
    margin-top: -5px;
    flex-grow: 2;

    color: white;
    font-weight: normal;
    font-size: xx-large;
    font-family: 'Yellowtail', cursive;
    
    text-align: center;
}


#holdMenuItems{
    width: 30%;


    display:flex;
    justify-content:center;
}

.menuItem{
    color: white;

    width: 33%;
    height: 25px;
    -webkit-padding-before: 5px;
            padding-block-start: 5px;

    text-align: center;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;

    border-style: solid;
    border-color: rgb(71, 71, 71);
    border-width: 2px;
    
}

.menuItem:nth-child(1){
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.menuItem:nth-child(3){
    margin-right: 10px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.links{
    color: white;
    text-decoration: none;
}

.menuItem:hover .links{
    color: black;
}
